.new-expance-controls{
    display: flex;
    flex-wrap: wrap;
}
.new-expance-control{
    margin: 0 15px 0 0;
}
.new-expance-control label{
    display: block;
    margin:  0 0 7px 0;
    font-weight: 700;
}
.new-expance-control input{
    width: 300px;
    height: 30px;
    border-radius: 5px;
    outline: none;
    border: none;
    font-weight: 500;
}

.new-expance-action{
    float: right;
    border: 1px solid white ;
    background: rgb(74, 9, 251);
    border-radius: 5px;
    padding: 10px;
}
.new-expance-action button{
    background:rgb(74, 9, 251) ;
    border: none;
    color: white;
}