.Subcomponent {
    display: flex;
    justify-content: space-between;
    border: 2px solid grey;
    border-radius: 10px;
    background: gray;
    margin:20px 0;
}
.Subcomponent-description{
    display: flex;
    justify-content: space-around;
    padding:0 30px ;
    color: white;
    align-items: center;
}


.Subcomponent-price{
    margin: auto;
    margin-left: 20px;
    padding: 10px ;
    font-weight: 700;
    border: 1px solid white;
    background: rgb(3, 36, 135);
    border-radius: 10px;
    text-align: center;
    align-items: center;
}
button{
    background: none;
    border: none;
    color: white;
}
.Subcomponent .button{
     
    right: 20%;
    margin-top: 3.5rem;
    position: absolute;
    padding:  6px;
    background: red;
    color: white;
    border:1px solid white;
    border-radius: 6px;
   

    

}